import React from "react";
import * as axios from "axios";


const Fake = () => {
  const handleRequest = () => {
    return axios
      .put(
        `https://social-network.samuraijs.com/api/1.0/profile/status`,
        { status: 'Bizdayam ishlarkan akramjon' },
        {
          withCredentials: true,
          header: {
            "API-KEY": "538ca928-4aa3-433c-aa91-b6255ff42d95",
            // "API-KEY": "f1070048-9a15-4932-b34c-2c25beb54035"
          },
        }
      )
      
  };

  return (
    <div>
      <h1> SINOV UCHUN MAYDON </h1>
      <button onClick={handleRequest}>Tasdiqlash</button>
    </div>
  );
};

export default Fake;
